<template>
    <div class="capital-subsidiary">
        <div class="capital-c">
            <div class="asset-title">
                <span class="desc"></span>
                <span class="name">{{$t('center.capitalSubsidiary[0]')}}</span>
            </div>
            <ul class="item assets clr">
                <span class="list label">{{$t('center.capitalSubsidiary[1]')}}</span>
                <!-- <span class="list all">{{$t('center.capitalSubsidiary[2]')}}</span> -->
                <li 
                class="list list-detail" 
                :class="{active: isAssets == i}" 
                v-for="(item,i) in assets" 
                :key="i" @click="chioseAssets(i,item.pid, item.pname)">{{ item.pname }}</li>
            </ul>
            <!-- <ul class="item type clr">
                <span class="list label">类型</span>
                <span class="list all">全部</span>
                <li 
                class="list list-detail" 
                :class="{active: isType == i}" 
                v-for="(item,i) in type" 
                :key="i" @click="chioseType(i)">{{ item }}</li>
            </ul> -->
            <template>
                <el-table
                :data="tableData"
                v-loading="tableLoading"
                style="width: 100%">
                    <el-table-column
                        prop="date"
                        :label="$t('center.capitalSubsidiary[3]')">
                        <template slot-scope="scope">
                            {{ $public.timestampToTime(scope.row.addtime) }}
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="price"
                        :label="$t('center.capitalSubsidiary[4]')">
                        <template slot-scope="scope">
                           <span style="color: red;" v-if="scope.row.price < 0">{{ scope.row.price }}</span>
                           <span style="color: green;" v-if="scope.row.price > 0">+{{ scope.row.price }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        :label="$t('center.capitalSubsidiary[5]')">
                        <template slot-scope="scope">
                           <span style="color: red;" v-if="scope.row.state == 0">{{$t('center.capitalSubsidiary[7]')}}</span>
                           <span style="color: green;" v-if="scope.row.state == 1">{{$t('center.capitalSubsidiary[8]')}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="memo"
                        :label="$t('center.capitalSubsidiary[6]')">
                    </el-table-column>
                </el-table>
                <pagination :page="page" @change="pageChange"></pagination>
            </template>
        </div>
    </div>
</template>

<script>
import  pagination from "@/components/pagination";
import { reCaiwu, reAsset, Asset } from '../../../api/c2c'
export default {
    name: 'capitalSubsidiary',
    data() {
        return {
            tableLoading: false,
            page:{
                size: 8,
                p: 1,
                total: 10
            },
            activeName: 'USDT',
            activeId: 'USDT',
            isAssets: 0,
            isType: 0,
            assets: [],
            type: [this.$t('center.capitalSubsidiary[9]'),this.$t('center.capitalSubsidiary[10]'),this.$t('center.capitalSubsidiary[11]'),this.$t('center.capitalSubsidiary[12]'),this.$t('center.capitalSubsidiary[13]')],
            tableData: []
        }
    },
    created() {
        this.getUsdtRe()
    },
    methods: {
        pageChange(row){
            this.page.p = row
            if (this.activeName == 'USDT') {
                this.getUsdtRe()
            } else {
                this.getOtherCoin()
            }
        },
        getUsdtRe() {
            this.tableLoading = true
            reCaiwu({ pid: 'USDT', p: this.page.p, size: this.page.size }).then(res=> {
                if (res.data.status == 200) {
                    this.tableLoading = false
                    this.tableData = res.data.data.res
                    this.page.total = res.data.data.count
                    return
                } else {
                    this.$public.msg(res.data.msg, 'error', this)
                    return
                }
            }),
            // 获取所有币种
            Asset().then(res=> {
                if (res.data.status == 200) {
                    this.assets = res.data.data.asset
                    return
                }
            })
        },
        chioseAssets(num, item, pNmae) {
            this.page.p = 1
            this.isAssets = num
            this.activeName = pNmae
            this.activeId = item
            if (pNmae == 'USDT') {
                this.getUsdtRe()
            } else {
                this.getOtherCoin()
            }
        },
        getOtherCoin() {
            this.tableLoading = true
            reAsset({ pid: this.activeId, p: this.page.p, size: this.page.size }).then(res=> {
                if (res.data.status == 200) {
                    this.tableLoading = false
                    this.tableData = res.data.data.res
                    this.page.total = res.data.data.count
                } else {
                    this.$public.msg(res.data.msg, 'error', this)
                    return
                }
            })
        },
        chioseType(num) {
            this.isType = num
        }
    },
    components: {
        pagination,
    }
}
</script>


<style lang=less>
  @import '../../../assets/index.less';
  @import './capitalSubsidiary.less';
</style>


